import sonCommentBox from './sonCommentBox.vue'
export default {
	components: {
		sonCommentBox,
	},
	
	data() {
		// æˆ‘æŠ„äº†ä¸€å°éƒ¨åˆ†ä»£ç è¿‡æ¥ï¼Œè¿˜æ²¡æ”¹ -Guetta
		// O(âˆ©_âˆ©)O
		// ðŸ˜„
		// ðŸ¤­
		// (â—'â—¡'â—)
		// é¡µé¢æœ‰ç‚¹ä¸‘ï¼ˆé«˜ä»¿å¾®åšï¼‰ï¼Œå›žå¤´è®©ä»…ä»…ä¼˜åŒ–ä¸€ä¸‹
		return {
			mainComment:'',    //ç”¨äºŽæŽ¥å—è·³è½¬ä¼ è¿‡æ¥çš„underCommentId,ç„¶åŽç”³è¯·èŽ·å–sonComment  yaoyao 9.16 
			userInfo: '',
			commentContent: '',  //ç”¨æˆ·å‡†å¤‡æäº¤çš„è¯„è®ºå†…å®¹
			commentList: '',  //è¿”å›žå€¼ï¼ŒèŽ·å–è¯„è®ºåˆ—è¡¨ä¿¡æ¯,å¾ªçŽ¯å±•ç¤ºçš„ä¸œè¥¿ï¼ŒsonComment
			showInput:false,  //æŽ§åˆ¶è¾“å…¥æ¡†ï¼Œtrueæ—¶æ˜¾ç¤ºè¾“å…¥æ¡†
			writingComment:false,  //æŽ§åˆ¶è¾“å…¥æ¡†ï¼Œtrueæ—¶è‡ªåŠ¨èŽ·å–ç„¦ç‚¹ï¼Œæ‹‰èµ·è¾“å…¥æ³•
			submitData:{},
			placeholderText: 'è¯„è®ºç‚¹ä»€ä¹ˆå§......',
			
			totalPage: 1,
			currentPage: 1,
		}
